h1,h2,h3,h4,div,a{
    font-family: $tipografia-sans-serif;
}
body{
    margin: 0;
}

.header{
    display:grid;
    grid-template-columns: repeat(12,1fr);
    min-height: 10vh; //viewport height
    height: auto;
    background-color: $header-color-fondo;
    color:$header-color-letra;

    .header__titulo{
        grid-column:2/9;
        height: 100%;
        background-color: aquamarine;
    }

    .header__nav{
        grid-column: 9/12;
        height: 100%;
        background-color: rgb(55, 0, 255);
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        
        .nav__hamburguer{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            .nav__span{
                width:30px;
                height:4px;
                margin:2px;
                border-radius: 2px;
                background-color: black;
                transition: all 0.2s ease-out;
            }
        }
        .nav__hamburguer:hover{
            .nav__span:nth-child(2){
                width: 60%;
                background-color: red;
            }
        }
        
        
    }
}